import React from 'react'
import { Link } from 'gatsby'
import './style.scss'
import "animate.css/animate.min.css";
import ScrollAnimation from 'react-animate-on-scroll';

class Navi extends React.Component {
  render() {
    const { location, title } = this.props
    return (
      
      <nav className="navbar navbar-expand navbar-dark flex-md-row bg-transparent fixed-top">
        <div className="container-fluid">
          <div className="navbar-nav-scroll">
            <Link className="text-center" to="/">
              <h2 className="navbar-brand mb-0">{title}</h2>
            </Link>
            <ScrollAnimation animateIn='fadeIn' animateOut="fadeOut" offset={450} duration={0.3}>
            <p className="hr-h"></p>
            <ul className="navbar-nav bd-navbar-nav">
              <li
                className={
                  location.pathname === '/profile/' ? 'nav-item active' : 'nav-item'
                }
              >
                <Link to="/about/" className="nav-link">
                  About
                </Link>
              </li>
              <li
                className={'nav-item'}
              >
                <a href="https://work.yentsenliu.com" target="blank" className="nav-link">
                  Blog
                </a>
              </li>
              <li
                className={'nav-item'
                }
              >
                <a href="https://medium.com/@imyentsen/" target="blank" className="nav-link">
                  Blog
                </a>
              </li>
              <li
                className={
                  location.pathname === '/contact/'
                    ? 'nav-item active'
                    : 'nav-item'
                }
              >
                <Link to="/contact/" className="nav-link">
                  Contact
                </Link>
              </li>
            </ul>
            </ScrollAnimation>
          </div>
          <div className="navbar-nav flex-row ml-md-auto d-none d-md-flex" />
        </div>
      </nav>
    
    )
  }
}

export default Navi
